.footer {
  position: relative;
  float: left;
  width: 100%;
  background-color: #000;

  &__logo {
    @include display-flex(flex);
    @include flex-direction(column);
    position: relative;
    float: left;
    width: 20%;

    .logo {
      position: relative;
      float: left;
      width: 125px;
      height: 140px;
      overflow: hidden;
      margin-top: 15px;

      img, svg {
        size: 100%;
        object-fit: contain;
      }
    }

    &--title {
      position: relative;
      float: left;
      width: 100%;
      margin-top: 20px;
      margin-bottom: 0;
      color: #d6b780;
      font-size: 18px;
      font-family: $ProximaNovaBold;
      text-transform: uppercase;
    }

    &--address {
      position: relative;
      float: left;
      width: 100%;
      margin: 0;
      color: #d6b780;
      font-size: 18px;
      font-family: $ProximaNovaRegular;
      text-transform: uppercase;
    }

    &--call {
      position: relative;
      float: left;
      width: 100%;
      margin: 0;
      color: #d6b780;
      font-size: 18px;
      font-family: $ProximaNovaRegular;
      text-transform: uppercase;

      &:last-of-type {
        margin-top: 30px;
        margin-bottom: 50px;
      }
    }
  }

  &__element {
    @include display-flex(flex);
    @include flex-direction(column);
    position: relative;
    float: left;
    width: 25%;
    padding-left: 30px;
    @include box-sizing(border-box);

    &--title {
      position: relative;
      float: left;
      width: 100%;
      margin-top: 50px;
      margin-bottom: 22px;
      text-align: left;
      color: #fffef6;
      font-size: 18px;
      font-family: $ProximaNovaBold;
      text-transform: uppercase;
    }

    &--menu {
      position: relative;
      float: left;
      width: 100%;
      margin: 0;
      padding: 0;
      list-style: none;

      li {

        > a {
          position: relative;
          float: left;
          width: 100%;
          line-height: 2.0;
          text-align: left;
          color: #fffef6;
          font-size: 18px;
          font-family: $ProximaNovaRegular;
          text-transform: uppercase;
        }

        > a.footer__element--email {
          text-transform: lowercase;
        }

      }

    }



  }

  &__partner {
    @include display-flex(flex);
    @include flex-direction(column);
    position: relative;
    float: left;
    width: 196px;
    padding-left: 30px;
    @include box-sizing(border-box);

    &--title {
      position: relative;
      float: left;
      width: 100%;
      margin-top: 50px;
      margin-bottom: 22px;
      text-align: left;
      color: #fffef6;
      font-size: 18px;
      font-family: $ProximaNovaBold;
      text-transform: uppercase;
    }

    &--partner {
      position: relative;
      float: left;
      width: 116px;
      height: 30px;
      overflow: hidden;
      margin-top: 10px;

      img, svg {
        size: 100%;
        object-fit: contain;
      }
    }

  }

  &__socials {
    @include display-flex(flex);
    @include justify-content(flex-end);
    position: relative;
    float: left;
    width: calc(100% - (20% + (25%*2 + 196px)));
    margin-top: 43px;

    a {
      position: relative;
      float: left;
      width: 30px;
      height: 30px;
      margin: 0 5px;
      overflow: hidden;
      border-radius: 50%;
      background-color: #fffef6;
    }

  }
}